"use client";

import React, { useState, useEffect } from 'react';
import Layout from '@/components/layout';

interface User {
  name: string;
  email: string;
}

export default function ProfilePage() {
  const [user, setUser] = useState<User | null>(null);

  useEffect(() => {
    async function fetchUserData() {
      const token = localStorage.getItem('token');
      if (token) {
        try {
          const response = await fetch('http://localhost:5000/user', {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          });
          if (response.ok) {
            const userData = await response.json();
            setUser(userData);
          } else {
            console.error('Failed to fetch user data');
          }
        } catch (error) {
          console.error('Error fetching user data:', error);
        }
      }
    }

    fetchUserData();
  }, []);

  return (
    <Layout>
      <div className="min-h-screen flex flex-col items-center justify-start bg-[#e9dccb] text-white p-8">
        <h1 className="text-3xl font-semibold mb-8">Profile</h1>
        {user && (
          <div className="bg-white text-gray-800 p-8 rounded-lg shadow-md w-full max-w-md">
            <div className="mb-4">
              <strong className="block mb-1">Name:</strong>
              <span>{user.name}</span>
            </div>
            <div className="mb-4">
              <strong className="block mb-1">Email:</strong>
              <span>{user.email}</span>
            </div>
            {/* Add more user information fields here */}
          </div>
        )}
      </div>
    </Layout>
  );
}